<template>
  <div>
    <div class="atom-spinner" id="loaderc" v-if='!renderPage'>
      <div class="spinner-inner">
        <div class="spinner-line"></div>
        <div class="spinner-line"></div>
        <div class="spinner-line"></div>
        <div class="spinner-circle">
          &#9679;
        </div>
      </div>
    </div>
    <datetime auto :phrases="{cancel: 'Cancel'}" v-model="dat" v-if='renderPage'></datetime>
    <br>
    <div class="fancy-box-wrapper row" v-if='renderPage'>
      <div class="box-wrapper">
          <div class="bttn-warning" style="margin: 23% auto;" v-if='g == 0'>
            <h3>No Games For Today</h3>
          </div>
          <router-link v-else v-for='(game, index) in games'  :key="index" :to="{ name: league[r][5], params: { id: game.gamePk }}">
            <!--<div :class="g % 3 == 0 ? (g % 2 == 0 ? 'col-md-6 col-lg-4' : (index > g - 2 ? 'col-md-12 col-lg-4' : 'col-md-6 col-lg-4')) : (g % 3 == 1 ? (g % 2 == 0 ? (index % 5 > 2 ? 'col-md-6 col-lg-6' : 'col-md-6 col-lg-4') : (index % 5 > 2 ? 'col-md-12 col-lg-6' : 'col-md-12 col-lg-4')) : (g % 2 == 0 ? (index % 5 > 2 ? 'col-md-6 col-lg-6' : 'col-md-6 col-lg-4') : (index % 5 > 2 ? 'col-md-12 col-lg-6' : 'col-md-12 col-lg-4')))" style="float: left;">-->
            <div class="col-md-6 col-lg-4" style="float: left;">
              <div class="box">

                <div style="height:98px;">

                  <div style="float: left;width:36%">
                    <router-link :to="{ name: league[r][2], params: { id: game.teams.away.team.id }}">
                      <img :alt="game.teams.away.team.teamName" style="height: 70px;padding: 0.25rem;border-radius: 50%;border: 1px solid #eea93a;" v-bind:src="GetLogo(game.teams.away.team.abbreviation,r)">
                      <div class="title">{{game.teams.away.team.teamName}}</div>
                    </router-link>
                  </div>

                  <div class="xs hint--rounded hint--bounce" style="margin-top: 27.5px;width:28%">
                    <b>{{game.status.abstractGameState == 'Live' || game.status.abstractGameState == 'Final' ? game.status.abstractGameState : LocalTime(game.gameDate)}}</b><br>
                    <b v-if="game.broadcasts && r == 'nhl'">{{game.broadcasts[0].name}}<br></b>
                    <b v-if="game.broadcasts && r == 'mlb' && game.broadcasts[0].type == 'TV'">{{game.broadcasts[0].callSign}}<br></b>
                    <b v-else-if="game.broadcasts && game.broadcasts[1] && r == 'mlb' && game.broadcasts[1].type == 'TV'">{{game.broadcasts[1].callSign}}<br></b>
                    <b v-else-if="game.broadcasts && game.broadcasts[2] && r == 'mlb' && game.broadcasts[2].type == 'TV'">{{game.broadcasts[2].callSign}}<br></b>
                    <b v-else-if="game.broadcasts && game.broadcasts[3] && r == 'mlb' && game.broadcasts[3].type == 'TV'">{{game.broadcasts[3].callSign}}<br></b>
                    <b v-else-if="game.broadcasts && game.broadcasts[4] && r == 'mlb' && game.broadcasts[4].type == 'TV'">{{game.broadcasts[4].callSign}}<br></b>
                    <b v-else-if="r == 'mlb'">No Feeds<br></b>    

                  </div>

                  <div style="float: right;width:36%">
                    <router-link :to="{ name: league[r][2], params: { id: game.teams.home.team.id }}">
                      <img :alt="game.teams.home.team.teamName" style="height: 70px;padding: 0.25rem;border-radius: 50%;border: 1px solid #eea93a;" v-bind:src="GetLogo(game.teams.home.team.abbreviation,r)">
                      <div class="title">{{game.teams.home.team.teamName}}</div>
                    </router-link>
                  </div>

                </div>

              </div>
              </div>
          </router-link>
        </div>
    </div>
  </div>
</template>

<script>
const axios = require('axios')
const {DateTime} = require("luxon");
export default {
  name: 'games',
  data () {
    var local = DateTime.local().setZone("America/Los_Angeles")
    let dt = this.$route.params.date ? this.$route.params.date : local.toISODate()
    return {
      date:dt,
      games:{},
      dat:dt+'T00:00:00.000Z',
      g:null,
      renderPage:false,
      user: localStorage.user,
      league: {
        'nhl': ['nhl', 'NHL', 'nhlteam', 'https://statsapi.web.nhl.com/api/v1/teams','https://statsapi.web.nhl.com/api/v1/','nhlgame'],
        'mlb': ['mlb', 'MLB', 'mlbteam', 'https://statsapi.mlb.com/api/v1/teams?sportId=1','https://statsapi.mlb.com/api/v1/','mlbgame']},
      r: this.$route.name == 'nhl' ? 'nhl' : 'mlb'
    }
  },
  mounted () {
     this.Load()
  },
  watch:{
    dat: function(dat){
      let datc = DateTime.fromISO(dat).toISODate()
      this.$router.push('/'+this.r+'/'+datc)
      this.Load(datc)
    }
  },
  methods: {
    Load(dat){
      let vm = this
      if(dat){
        this.date=dat
        this.renderPage = false
      }
      axios.get(vm.league[vm.r][4]+'schedule?startDate='+vm.date+'&endDate='+vm.date+'&sportId=1&hydrate=team(leaders(categories=[points,goals,assists],gameTypes=[R])),linescore,broadcasts(all),tickets,game(content(media(epg),highlights(scoreboard)),seriesSummary),radioBroadcasts,metadata,decisions,scoringplays,seriesSummary(series)')
        .then(function (res) {
          vm.g = res.data.totalGames
          if(vm.g > 0 ){
          vm.games = res.data.dates[0].games
        }
        vm.renderPage = true
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    LocalTime(x){
      return LocalTime(x);
    },
    GetLogo(x,y){
      return GetLogo(x,y);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .card .card-body .form-group {margin: 2rem 0;}
    button.bttn-float.bttn-xs.bttn-warning.my-4 {
    width: 32%;
    margin: 0.125rem;
    padding: 0.5rem;
    font-weight: 600;
    font-size: 13px;
    }
    .title {
    font-weight: 500;
    font-size: 0.9em;
    margin-top: 0.3rem;
}
</style>



