import Vue from 'vue'
import Router from 'vue-router'
import games from '@/components/games'
import nhlgame from '@/components/nhlgame'
import mlbgame from '@/components/mlbgame'
import team from '@/components/team'
import teams from '@/components/teams'
import video from '@/components/video'
import home from '@/components/home'
import donate from '@/components/donate'


import Datetime from 'vue-datetime'
import 'vue-datetime/dist/vue-datetime.css'
Vue.use(Datetime)
Vue.extend({
    template: '<datetime/>',
    components: {
        datetime: Datetime
    }
});

Vue.use(Router)

export default new Router({
    mode: 'history',
    base: __dirname,
    routes: [
        {
            path: '/mlb/teams',
            name: 'mlbteams',
            component: teams
        },
        {
            path: '/donate',
            name: 'donate',
            component: donate
        },
        {
            path: '/mlb/team/:id',
            name: 'mlbteam',
            component: team
        },
        {
            path: '/nhl/teams',
            name: 'nhlteams',
            component: teams
        },
        {
            path: '/nhl/team/:id',
            name: 'nhlteam',
            component: team
        },
        {
            path: '/nhl/game/:id/:cdn?/:feed?',
            name: 'nhlgame',
            component: nhlgame
        },{
            path: '/mlb/game/:id/:cdn?/:feed?',
            name: 'mlbgame',
            component: mlbgame
        },
        {
            path: '/mlb/:date?',
            name: 'mlb',
            component: games
        },
        {
            path: '/nhl/:date?',
            name: 'nhl',
            component: games
        },
        {
            path: '/video/:vid/:home/:away/:feed?',
            name: 'video',
            component: video
        },
        {
          path: '/:date?',
          name: 'home',
          component: home
        }
    ]
})



