<template>
  <div>
    <div class="atom-spinner" id="loaderc" v-if='!renderPage && !error'>
      <div class="spinner-inner">
        <div class="spinner-line"></div>
        <div class="spinner-line"></div>
        <div class="spinner-line"></div>
        <div class="spinner-circle">
          &#9679;
        </div>
      </div>
    </div>
    <div class="bttn-warning" v-if='error'>
     <h3>Invalid Team</h3><br/>
    </div>
    <div v-if='renderPage'>
    <div class="bttn-warning">
      <h3>{{team.name}} Games Archive 2018/2019 </h3><br/>
    </div>
    <div class="fancy-box-wrapper">
     <div id="games" class="box-wrapper">
      <router-link v-for='(game, index) in games.slice().reverse()' :key="index" :to="{ name: league[r][5], params: { id: game.games[0].gamePk }}">
        <div class="col-md-6 col-lg-4" style="float: left;">
          <div class="box">

            <div style="height:98px;">

              <div style="float: left;width:36%">
                <router-link :to="{ name: league[r][2], params: { id: game.games[0].teams.away.team.id }}">
                  <img :alt="game.games[0].teams.away.team.teamName" style="height: 70px;padding: 0.25rem;border-radius: 50%;border: 1px solid #eea93a;" v-bind:src="GetLogo(game.games[0].teams.away.team.abbreviation,r)">
                  <div class="title">{{game.games[0].teams.away.team.teamName}}</div>
                </router-link>
              </div>

              <div class="xs hint--rounded hint--bounce" style="margin-top: 27.5px;width:28%">
                <b v-if='game.games[0].status.abstractGameState == "Final" && score == "on"'>{{game.games[0].teams.away.score}}-{{game.games[0].teams.home.score}}<br></b>
                <b v-else>{{LocalTime(game.games[0].gameDate)}}<br></b>
                <b>{{game.date}}</b><br>
              </div>

              <div style="float: right;width:36%">
                <router-link :to="{ name: league[r][2], params: { id: game.games[0].teams.home.team.id }}">
                  <img :alt="game.games[0].teams.home.team.teamName" style="height: 70px;padding: 0.25rem;border-radius: 50%;border: 1px solid #eea93a;" v-bind:src="GetLogo(game.games[0].teams.home.team.abbreviation,r)">
                  <div class="title">{{game.games[0].teams.home.team.teamName}}</div>
                </router-link>
              </div>

            </div>

          </div>
          </div>
      </router-link>
     </div>
    </div>
    </div>
  </div>
</template>
<script>
const {DateTime} = require("luxon");
const axios = require('axios')
export default {
  name: 'team',
  data () {
    return {
      team: [],
      games: [],
      id:this.$route.params.id,
      renderPage: false,
      error:false,
      user: localStorage.user,
      score: this.$parent.score,
      league: {
        'nhl': ['nhl', 'NHL', 'nhlteam', 'https://statsapi.web.nhl.com/api/v1/teams/','https://statsapi.web.nhl.com/api/v1/schedule?startDate=2018-10-04&expand=schedule.teams,schedule.linescore,schedule.game.content.media.epg&sportId=1&endDate=','nhlgame'],
        'mlb': ['mlb', 'MLB', 'mlbteam', 'https://statsapi.mlb.com/api/v1/teams/','https://statsapi.mlb.com/api/v1/schedule?startDate=2019-02-20&expand=schedule.teams,schedule.linescore,schedule.game.content.media.epg&sportId=1&endDate=','mlbgame']},
      r: this.$route.name == 'nhlteam' ? 'nhl' : 'mlb'
    }
  },
  watch:{
    $route: function(id){
      this.renderPage=false
      this.Load(this.$route.params.id)
    }
  },
  mounted () {
      this.Load()
  },
  methods: {
    Load(id){
      let vm = this
      if(id){vm.id=id}
      if (localStorage.getItem(vm.r+'_'+vm.id)) {
          vm.team = JSON.parse(localStorage.getItem(vm.r+'_'+vm.id))
        }
      else{
      axios.get(vm.league[vm.r][3]+vm.id+'?sportId=1')
        .then(function (res) {
          vm.team = res.data.teams[0]
          const gm = JSON.stringify(vm.team)
          localStorage.setItem(vm.r+'_'+vm.id,gm)
        })
        .catch(function (err) {
          vm.error = true
          console.log(err)
        })
      }
      let date = DateTime.local().toISODate();
      axios.get(vm.league[vm.r][4]+date+'&teamId='+vm.id)
        .then(function (res) {
          vm.games = res.data.dates
          vm.renderPage = true
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    LocalTime(x){
      return LocalTime(x);
    },
    GetLogo(x,y){
      return GetLogo(x,y);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h6 i {
    float: right;
    margin: 0 0.25rem;
    color: #eca535;
  }
  .card-icon img {
    width: 70px;
  }
  h6.i {
      text-align: left;
      margin-left: 1.2rem;
  }
  .dark-edition .card-profile .card-avatar {background: #051724;}
  .dark-edition .card .card-header-warning .card-icon, .dark-edition .card .card-header-warning .card-text, .dark-edition .card .card-header-warning:not(.card-header-icon):not(.card-header-text), .dark-edition .card.bg-warning, .dark-edition .card.card-rotate.bg-warning .front, .dark-edition .card.card-rotate.bg-warning .back {
      background: linear-gradient(120deg,#051925,#f4aa39);
  }
  .card [class*=card-header-] .card-icon, .card [class*=card-header-] .card-text {
      border-radius: 3px;
      background-color: #999;
      padding: 5px;
      margin-top: -20px;
      margin-right: 15px;
      float: left;
  }
  .card-profile .card-avatar {
    padding: 0.5rem;
}
.title {
    font-weight: 500;
    font-size: 0.9em;
    margin-top: 0.3rem;
}
</style>



